import { graphql, useStaticQuery } from "gatsby";
import type { Post } from "@/lib/types/post";

const useTechPosts = () => {
	const data = useStaticQuery(graphql`
    query TechBlogPosts {
      tech: allMdx(
        filter: { internal: { contentFilePath: { regex: "/posts/tech/" } } }
        sort: { frontmatter: { date: DESC } }
      ) {
        edges {
          node {
            fields {
              timeToRead {
                minutes
                text
                time
                words
              }
            }
            frontmatter {
              title
              description
              date
            }
            internal {
              contentFilePath
            }
          }
        }
      }
    }
  `);
	return data.tech.edges as Post[];
};

export { useTechPosts };
